import * as React from 'react'
import {
  Section,
  Button,
  ToggleButton,
  Table,
  Avatar,
  P,
  Dl,
  Tr,
  Th,
  Td,
  NumberFormat,
} from '@dnb/eufemia'
import { shopping_cart } from '@dnb/eufemia/icons'

const buttonClickHandler = (e: React.SyntheticEvent) => {
  e.stopPropagation()
  e.preventDefault()
  console.log('buttonClickHandler')
}

const HeaderSortButton = ({ children }: { children: React.ReactNode }) => {
  return (
    <Button
      variant="tertiary"
      icon="arrow-down"
      text={children}
      title="Sort table column"
      // wrap="true"
    />
  )
}

const ContentTr = ({
  open = false,
  noAnimation = false,
  accordion = false,
} = {}) => (
  <Tr open={open} noAnimation={noAnimation}>
    <Td.MainCell
      title="Sbanken Global Indeks"
      info="Globalt indeksfond"
      avatar={
        <Avatar.Group aria-hidden label="Sbanken Global Indeks">
          <Avatar>S</Avatar>
        </Avatar.Group>
      }
      // alwaysToggleAccordionContent
      // onClick={() => {

      // }}
    />

    <Td info="1 år">
      <P
        element="span"
        size="x-small"
        style={{ color: 'var(--color-success-green)' }}
      >
        +<NumberFormat percent>17.24</NumberFormat>
      </P>
    </Td>

    <Td info="Kostnad 1">
      <P element="span" size="x-small">
        <NumberFormat percent>0.5</NumberFormat>
      </P>
    </Td>

    <Td info="Kostnad 2">
      <P element="span" size="x-small">
        <NumberFormat percent>0.8</NumberFormat>
      </P>
    </Td>

    <Td info={<>M.</>}>{'⭐️⭐️⭐️⭐️'}</Td>

    <Td align="right">
      <div>
        <Button variant="secondary" icon={shopping_cart} icon_position="left">
          Legg til
        </Button>
      </div>
    </Td>

    {accordion ? (
      <Table.AccordionContent>
        <Dl>
          <dt>Favorittfarge</dt>
          <dd>Grønn</dd>
          <dt>
            Favorittmat <button onClick={buttonClickHandler}>button</button>
          </dt>
          <dd>
            Taco <button onClick={buttonClickHandler}>button</button>
          </dd>
        </Dl>
      </Table.AccordionContent>
    ) : (
      <></>
    )}
  </Tr>
)

const TableContent = ({ accordion = false } = {}) => {
  const manyRows = []
  for (let i = 0; i < 2; i++) {
    manyRows.push(<ContentTr key={i} accordion={accordion} />)
  }
  return (
    <>
      {/* <caption>A Table Caption</caption> */}
      <thead>
        <Tr>
          <Th
            scope="col"
            // colSpan={2}
            className="dnb-table--sortable dnb-table--active"
          >
            <HeaderSortButton>Fond</HeaderSortButton>
          </Th>
          <Th scope="col" className="dnb-table--sortable dnb-table--reversed">
            <HeaderSortButton>1 år</HeaderSortButton>
          </Th>
          <Th scope="col" className="dnb-table--sortable">
            <HeaderSortButton>Kostnad 1</HeaderSortButton>
          </Th>
          <Th scope="col" className="dnb-table--sortable">
            <HeaderSortButton>Kostnad 2</HeaderSortButton>
          </Th>
          <Th scope="col" className="dnb-table--sortable">
            <HeaderSortButton>Morningstar</HeaderSortButton>
          </Th>
          <Th scope="col" align="right" className="dnb-table--sortable">
            <HeaderSortButton>Handlekurv</HeaderSortButton>
          </Th>
        </Tr>
      </thead>

      <tbody>
        <ContentTr
          // open
          // noAnimation
          accordion={accordion}
        />

        {manyRows}
      </tbody>
    </>
  )
}

const ResponsiveTable = () => {
  const [skeleton, setSkeleton] = React.useState(false)
  return (
    <main aria-label="main area">
      <h1 className="dnb-sr-only">responsive tables</h1>
      <ToggleButton
        // size="small"
        on_change={() => {
          setSkeleton((s: boolean) => !s)
        }}
      >
        Toggle Skeleton
      </ToggleButton>

      <h2>cards</h2>

      <Table skeleton={skeleton} lang="no" top variant="cards">
        <TableContent />
      </Table>

      <h2>lines</h2>

      <Table skeleton={skeleton} lang="no" top variant="lines">
        <TableContent />
      </Table>

      <h2>accordion cards</h2>

      <Table skeleton={skeleton} lang="no" top variant="cards">
        <TableContent accordion />
      </Table>

      <h2>accordion lines</h2>

      <Table skeleton={skeleton} lang="no" top variant="lines">
        <TableContent accordion />
      </Table>
    </main>
  )
}

export default function App() {
  return (
    <Section spacing>
      <ResponsiveTable />
    </Section>
  )
}
